<template>
  <div class="main">
    <!-- ÂäüËÉΩÂå∫ -->
    <div class="tool-content">
      <span class="btn" @click="changeLevel">Êõ¥Êç¢ÈöæÂ∫¶</span>
      <span>Èõ∑Êï∞Ôºö{{ gameInfo[2] - markNum }}</span>
      <span>Áî®Êó∂Ôºö{{ time }} s</span>
      <span class="btn" @click="reStart">ÈáçÊñ∞ÂºÄÂßã</span>
    </div>
    <!-- Ê∏∏ÊàèÂå∫ -->
    <div class="game-content">
      <div
        v-for="col in cols"
        :key="Math.random() + col"
        class="game-content-row"
      >
        <span
          v-for="row in rows"
          :key="Math.random() + row"
          class="game-block"
          :class="[
            lattice[(col - 1) * rows + row - 1].isOpen ? 'open' : '',
            over !== 0 &&
            (lattice[(col - 1) * rows + row - 1].isMark ||
              lattice[(col - 1) * rows + row - 1].isMine)
              ? 'open'
              : '',
          ]"
          @click.left="leftClick(lattice[(col - 1) * rows + row - 1])"
          @click.right.prevent="rightClick(lattice[(col - 1) * rows + row - 1])"
          @dblclick="ondblClick(lattice[(col - 1) * rows + row - 1])"
        >
          <span v-if="over !== 0 && lattice[(col - 1) * rows + row - 1].isMine"
            >üí£</span
          >
          <span
            v-else-if="over === 0 && lattice[(col - 1) * rows + row - 1].isMark"
            >üö©</span
          >
          <span v-else-if="lattice[(col - 1) * rows + row - 1].mineNum !== 0">{{
            lattice[(col - 1) * rows + row - 1].mineNum
          }}</span>
          <span v-else>&nbsp;</span>
        </span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "mineSweeping",
  components: {},
  props: {
    // ÊòØÂê¶Â±ïÁ§∫Ê∏∏ÊàèÁõò
    showGame: {
      required: true,
      type: Boolean,
    },
    // Ê∏∏ÊàèÁõòÊ†ºÂ≠êÊï∞ÂíåÈõ∑Êï∞
    gameInfo: {
      required: true,
      type: Array,
    },
  },
  data() {
    return {
      // Ê®™ÊéíÊ†ºÂ≠êÊï∞
      rows: 0,
      // Á∫µÊéíÊ†ºÂ≠êÊï∞
      cols: 0,
      // Ê†ºÂ≠êÊÄªÊï∞
      latticeNum: 0,
      // Ê†áËÆ∞ÊÄªÊï∞
      markNum: 0,
      // ÁÇπÂºÄÊÄªÊï∞
      openNum: 0,
      // Èõ∑ÁÇπ‰ΩçÁΩÆÔºàÊâæÂá∫‰∏Ä‰∏™Â∞ë‰∏Ä‰∏™Ôºâ
      minePosition: [],
      // Èõ∑ÁÇπ‰ΩçÁΩÆÂ§á‰∏™‰ªΩ
      minePositionBake: [],
      // Ê†ºÂ≠êÂ±ûÊÄß
      lattice: {
        index: 0, // Ê†ºÂ≠êÁ¥¢Âºï
        mineNum: 0, // Âë®Âõ¥Èõ∑Êï∞
        isMine: false, // ÊòØÂê¶ÊòØÈõ∑
        isOpen: false, // ÊòØÂê¶Â∑≤ÁªèË¢´ÁÇπÂºÄ
        isMark: false, // ÊòØÂê¶Ë¢´Ê†áËÆ∞
      },
      // Ê∏∏ÊàèÊòØÂê¶ÁªìÊùüÔºö 0-Êú™ÁªìÊùü 1-ÁÇπÂà∞Èõ∑‰∫ÜÂØºËá¥ÁªìÊùü  2-Èõ∑ÁÇπË¢´Ê†áËÆ∞ÂÆå‰∫Ü
      over: 0,
      // Ê∏∏ÊàèËÆ°Êó∂
      time: 0,
      // Ê∏∏ÊàèËÆ°Êó∂Âô®
      interval: null,
      // ÊàêÂäüÂ±ÄÊï∞
      successNum: 0,
      // Â§±Ë¥•Â±ÄÊï∞
      failNum: 0,
    };
  },
  computed: {
    timeText() {
      return this.time;
    },
  },
  watch: {
    over(newVal) {
      switch (newVal) {
        case 1:
          this.failNum++;
          clearInterval(this.interval);
          setTimeout(() => {
            alert(`
            ÂæàÈÅóÊÜæÔºåÊÇ®Â§±Ë¥•‰∫Ü

            ÊàêÂäüÂ±ÄÊï∞Ôºö${this.successNum}
            Â§±Ë¥•Â±ÄÊï∞Ôºö${this.failNum}
            `);
          }, 0);
          break;
        case 2:
          this.successNum++;
          clearInterval(this.interval);
          setTimeout(() => {
            alert(`
            ÊÅ≠ÂñúÔºåÊâ´Èõ∑ÊàêÂäüÔºÅ

            ÊàêÂäüÂ±ÄÊï∞Ôºö${this.successNum}
            Â§±Ë¥•Â±ÄÊï∞Ôºö${this.failNum}
            `);
          }, 0);
          break;
      }
    },
  },
  created() {
    this.rows = this.gameInfo[0];
    this.cols = this.gameInfo[1];
    this.latticeNum = this.rows * this.cols;
    this.init();
    document.onselectstart = function () {
      return false;
    };
  },
  mounted() {
    this.setTime();
  },
  beforeDestroy() {
    clearInterval(this.interval);
  },
  methods: {
    init() {
      this.markNum = 0;
      this.openNum = 0;
      this.minePosition = [];
      this.minePositionBake = [];
      this.lattice = {
        index: 0, // Ê†ºÂ≠êÁ¥¢Âºï
        mineNum: 0, // Âë®Âõ¥Èõ∑Êï∞
        isMine: false, // ÊòØÂê¶ÊòØÈõ∑
        isOpen: false, // ÊòØÂê¶Â∑≤ÁªèË¢´ÁÇπÂºÄ
        isMark: false, // ÊòØÂê¶Ë¢´Ê†áËÆ∞
      };
      this.over = 0;
      this.time = 0;
      // Ëé∑ÂèñÈõ∑ÁÇπ‰ΩçÁΩÆ
      this.getMinePosition();
      // ÁªôÊØè‰∏™Ê†ºÂ≠êËµã‰∫àÊ≠£Á°ÆÁöÑÂ±ûÊÄß
      this.initLattice();
    },
    // ÂºÄÂßãËÆ°Êó∂
    setTime() {
      clearInterval(this.interval);
      this.interval = setInterval(() => {
        this.time++;
      }, 1000);
    },
    // ÈöèÊú∫Ëé∑ÂèñÈõ∑ÁÇπ‰ΩçÁΩÆ
    getMinePosition() {
      // ÂÆö‰πâ‰∏Ä‰∏™Êï∞ÁªÑË£Ö‰∏çÈáçÂ§çÁöÑÊ†ºÁÇπ
      let mineArr = [];
      // Âæ™ÁéØÈõ∑Êï∞ÁîüÊàê‰∏çÈáçÂ§çÁöÑÈõ∑ÁÇπ
      for (let n = 0; n < this.gameInfo[2]; n++) {
        const random = Math.floor(Math.random() * this.latticeNum);
        if (mineArr.indexOf(random) === -1) {
          mineArr.push(random);
        } else {
          n--;
        }
      }
      this.minePosition = mineArr;
      this.minePositionBake = [].concat(mineArr);
    },
    // Ê†ºÂ≠êÂ±ûÊÄßÂàùÂßãÂåñ
    initLattice() {
      let latticeArr = [];
      for (let n = 0; n < this.latticeNum; n++) {
        let lattice = {
          index: n,
          isOpen: false,
          mineNum: 0,
          isMark: false,
        };
        // nÊ†áËÆ∞ÊòØÂê¶ÊòØÈõ∑
        lattice.isMine = this.minePosition.indexOf(n) > -1;
        // Â¶ÇÊûú‰∏çÊòØÈõ∑ÔºåËÆ°ÁÆóÂá∫Ê†ºÂ≠êÂë®Âõ¥8‰∏™ÁÇπÁöÑÈõ∑Êï∞
        if (!lattice.isMine) {
          this.getMineNumAroundLattice(lattice, n);
        }
        latticeArr.push(lattice);
      }
      this.lattice = latticeArr;
    },
    // Ëé∑ÂèñÊ†ºÂ≠êÂë®Âõ¥ÁöÑÈõ∑Êï∞Ôºå
    getMineNumAroundLattice(lattice, index) {
      // ÂÖàËé∑ÂèñÊ†ºÂ≠êÂë®Âõ¥ÁöÑÊúâÊïàÁ¥¢Âºï
      const latticeIndexArr = this.getLatticeIndex(index);
      // Âæ™ÁéØÁ¥¢ÂºïÔºåÁ¥¢ÂºïÂÄºÂú®Èõ∑ÁÇπÊï∞ÁªÑ‰∏≠ÁöÑÔºåÂç≥‰∏∫Èõ∑ÔºåÂΩìÂâçÊ†ºÂ≠êÁöÑÈõ∑ÁÇπÊï∞Âä†1
      latticeIndexArr.forEach((i) => {
        if (this.minePosition.indexOf(i) > -1) {
          lattice.mineNum++;
        }
      });
    },
    // Ëé∑ÂèñÊ†ºÂ≠êÂë®Âõ¥ÁöÑÊúâÊïàÁ¥¢Âºï
    getLatticeIndex(index) {
      // 0ÂÅöÁ¥¢Âºï‰∏çÂ•ΩÁÆóÔºåÊåâÊ≠£Â∏∏Êï∞Â≠óÊù•ÁÆó
      index++;
      // Â≠òÁ¥¢ÂºïÂÄºÁöÑÂèòÈáè
      let latticeIndexArr = [];
      // ÂΩìÂâçÊ†ºÂ≠ê‰Ωç‰∫éÁ¨¨Âá†Ë°å
      const latticeRow = Math.ceil(index / this.rows);
      // ÂΩìÂâçÊ†ºÂ≠ê‰Ωç‰∫éÁ¨¨Âá†ÂàóÔºàÊ±Ç‰Ωô‰∏∫0ËØ¥ÊòéÊòØÊúÄÂè≥Ëæπ‰∏ÄÂàóÔºâ
      const latticeCol = Math.ceil(index % this.rows) || this.rows;
      // const latticeCol = Math.ceil(index % this.rows) === 0 ? this.rows : Math.ceil(index % this.rows);
      // Á¨¨‰∏ÄË°åÊ≤°Êúâ‰∏ä‰∏ÄË°åÔºå‰∏çÈúÄË¶ÅËÆ°ÁÆóÂáè1ÁöÑË°åÂÄºÔºåÊúÄÂêé‰∏ÄË°åÊ≤°Êúâ‰∏ã‰∏ÄË°åÔºå‰∏çÈúÄË¶ÅËÆ°ÁÆóÂä†1ÁöÑË°åÂÄº
      for (
        let i = latticeRow === 1 ? 0 : -1;
        i < (latticeRow === this.cols ? 1 : 2);
        i++
      ) {
        // Á¨¨‰∏ÄÂàóÊ≤°ÊúâÂ∑¶ÂàóÔºå‰∏çÈúÄË¶ÅËÆ°ÁÆóÂáè1ÁöÑÂàóÂÄºÔºåÊúÄÂêé‰∏ÄÂàóÊ≤°ÊúâÂè≥ÂàóÔºå‰∏çÈúÄË¶ÅËÆ°ÁÆóÂä†1ÁöÑÂàóÂÄº
        for (
          let j = latticeCol === 1 ? 0 : -1;
          j < (latticeCol === this.rows ? 1 : 2);
          j++
        ) {
          // Á¥¢ÂºïÂÄº = (ÂΩìÂâçË°åÂÄº + Ôºà‰∏ä‰∏ÄË°å„Äê-1„Äë/ÂΩìÂâçË°å„Äê0„Äë/‰∏ã‰∏ÄË°å„Äê+1„ÄëÔºâ - 1„Äê1ÊòØÁ¥¢Âºï‰ªé0ÂºÄÂßãÔºåÊâÄ‰ª•ÈúÄË¶ÅÂáèÂéª„Äë) * ÊØèË°åÊ†ºÂ≠êÊï∞ + ÂΩìÂâçÂàóÂÄº + Ôºà‰∏ä‰∏ÄÂàó„Äê-1„Äë/ÂΩìÂâçÂàó„Äê0„Äë/‰∏ã‰∏ÄÂàó„Äê+1„ÄëÔºâ
          const latticeIndex =
            (latticeRow + i - 1) * this.rows + (latticeCol + j);
          // ÂàùÂßãÂÄºÂä†‰∫Ü1ÔºåÊâÄ‰ª•Á¥¢ÂºïË¶ÅÂáèÂéª1ÊâçÂØπ
          latticeIndexArr.push(latticeIndex - 1);
        }
      }
      return latticeIndexArr;
    },
    // Â∑¶ÈîÆÁÇπÂáª
    leftClick(lattice) {
      if (this.over) {
        return false;
      }
      // ÊòØÈõ∑ÔºåÊèêÂâçÁªìÊùüÊàòÊñó
      if (!lattice.isOpen && lattice.isMine) {
        lattice.isOpen = true;
        this.over = 1;
      } else {
        this.openNum++;
        // ÊòØÊï∞Â≠ó
        if (lattice.mineNum) {
          if (!lattice.isOpen && !lattice.isMark) {
            lattice.isOpen = true;
          }
        } else {
          // ÊòØÁ©∫ÁôΩ
          const latticeIndexArr = this.getLatticeIndex(lattice.index);
          this.showWhiteAround(lattice, latticeIndexArr);
        }
      }
    },
    // Âè≥ÈîÆÁ°ÆËÆ§ÊòØÈõ∑ÁÇπ
    rightClick(lattice) {
      if (this.over) {
        return false;
      }
      if (!lattice.isOpen) {
        lattice.isMark = true;
        lattice.isOpen = true;
        this.markNum++;
        this.openNum++;
        if (lattice.isMine)
          this.minePosition.splice(this.minePosition.indexOf(lattice.index), 1);
      } else if (lattice.isMark) {
        lattice.isMark = false;
        lattice.isOpen = false;
        this.markNum--;
        this.openNum--;
        if (lattice.isMine) this.minePosition.push(lattice.index);
      }
      if (this.markNum === this.gameInfo[2]) this.judgeIsOver();
    },
    // ÂΩìÂâçÊ†ºÂ≠êÂë®Âõ¥ÁöÑÈõ∑ÁÇπË¢´Ê≠£Á°ÆÊâæÂá∫Êó∂ÔºåÂèåÂáªÂ°´ÂÖÖÂë®Âõ¥Ê†ºÂ≠ê
    ondblClick(lattice) {
      console.log(1);
      // È¶ñÂÖàÂà§Êñ≠ÂΩìÂâçÊ†ºÂ≠êÂ∑≤Ë¢´ÁÇπÂºÄ
      if (lattice.isOpen && !lattice.isMark) {
        // ÊãøÂà∞Âë®Âõ¥ÊúâÊïàÊ†ºÂ≠êÁ¥¢Âºï
        const latticeIndexArr = this.getLatticeIndex(lattice.index);

        let markedNum = 0; // Ê†áËÆ∞ÁöÑÊï∞Èáè
        let mineAllMarked = false; // ÊòØÂê¶ÊúâÊ≤°ÊúâÊ†áËÆ∞ÁöÑÈõ∑
        let tempLatticeIndexArr = [];
        for (let i = 0; i < latticeIndexArr.length; i++) {
          if (this.lattice[latticeIndexArr[i]].isMark) {
            markedNum++;
          } else if (this.lattice[latticeIndexArr[i]].isMine) {
            mineAllMarked = true;
          } else {
            tempLatticeIndexArr.push(latticeIndexArr[i]);
          }
        }
        if (markedNum === lattice.mineNum) {
          if (mineAllMarked) this.over = 1;
          else {
            tempLatticeIndexArr.forEach((item) => {
              this.lattice[item - 1].isOpen = true;
            });
          }
        }
      }
    },
    // Âà§Êñ≠Ê∏∏ÊàèÊòØÂê¶ÁªìÊùü
    judgeIsOver() {
      this.over = this.minePosition.length === 0 ? 2 : 0;
    },
    // Âà§Êñ≠ÈîôËØØÁöÑÊ†áËÆ∞
    judgeWrongMark() {
      let wrongMark = 0;
      this.minePositionBake.forEach((item) => {
        if (!this.lattice[item].isMark) {
          wrongMark++;
        }
      });
      return wrongMark;
    },
    // Â±ïÁ§∫Âë®Âõ¥ÁöÑÁ©∫ÁôΩÊ†áËÆ∞ÔºåÁõ¥Ëá≥ËæπÁºòÔºàÊ†ºÂ≠êËæπÁºòÊàñËÄÖÊï∞Â≠óÔºâ
    showWhiteAround(lattice, latticeIndexArr) {
      // ÈÅøÂÖçÊúâÈáçÂ§çÁöÑÊï∞ÊçÆÂÅú‰∏ç‰∏ãÊù•ÔºåÂéª‰∏™Èáç
      latticeIndexArr = [...new Set(latticeIndexArr)];
      for (let i = 0; i < latticeIndexArr.length; i++) {
        const item = latticeIndexArr[i];
        // ËÆ°ÁÆó‰∏Ä‰∏™Â∞ë‰∏Ä‰∏™ÔºåÂáèÂ∞ëÂæ™ÁéØ
        latticeIndexArr.splice(i, 1);
        i--;
        if (this.lattice[item].isOpen) {
          continue;
        }
        this.lattice[item].isOpen = true;
        if (!this.lattice[item].mineNum) {
          const arr = this.getLatticeIndex(this.lattice[item].index);
          this.showWhiteAround(this.lattice[item], latticeIndexArr.concat(arr));
        }
      }
    },
    // ÈáçÂºÄ‰∏ÄÂ±Ä
    reStart() {
      this.init();
      this.setTime();
    },
    // ÊîπÂèòÈöæÂ∫¶
    changeLevel() {
      this.$emit("update:showGame", false);
    },
  },
};
</script>

<style lang="scss" scoped>
.main {
  width: 80%;
  margin: auto;
  margin-top: 100px;
  background-color: rgb(241, 241, 241);
  border: 5px dashed silver;
  border-radius: 10px;
  .tool-content {
    margin: 20px 5%;
    height: 30px;
    line-height: 30px;
    display: flex;
    justify-content: space-between;
    .btn {
      padding: 1px 10px;
      background-color: rgb(209, 204, 204);
      border-radius: 2px;
      text-align: center;
      cursor: pointer;
    }
  }
  .game-content {
    margin: 10px;
    overflow: auto;
    max-height: 70vh;
    .game-content-row {
      margin: 0 auto;
      width: fit-content;
      white-space: nowrap;
      .game-block {
        display: inline-block;
        margin: 1px;
        background-color: #d8afafcc;
        width: 30px;
        height: 30px;
        line-height: 30px;
        font-size: 14px;
        color: rgb(85, 69, 69);
        text-align: center;
        cursor: pointer;
        & > span {
          visibility: hidden;
        }
        &.open {
          background-color: #f7d8d79f;
          & > span {
            visibility: visible;
          }
        }
      }
    }
  }
}
</style>